<template>
    <div class="all_com">
        <div class="title_com">优惠码管理</div>
        <el-card class="box-card" :body-style="{'background-color': '#ffffff'}">
            <div slot="header" class="clearfix">
                <span>生成优惠码</span>
            </div>
            <el-form id='form' ref="form" label-position='top' :rules='rules' :model="form" label-width="80px">
                <el-form-item label="选择需要生成优惠码的活动" prop='activity_id'>
                        <el-select  placeholder="请选择" v-model="form.activity_id">
                        <el-option v-for='(item,index) in tableData' :key='index' :label="item.Name" :value="item.ID"></el-option>
                    </el-select>     
                </el-form-item>
                <el-form-item label="生成优惠码个数" prop='count'>
                    <el-input type='number' v-model="form.count"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">提交</el-button>
                </el-form-item>
            </el-form>
            <el-table v-if='list.length>0' :data="list" stripe style="width: 100%;height:100%">
                <el-table-column prop="ID" label="Id" >
                </el-table-column>
                <el-table-column prop="Code" label="激活码">
                    
                </el-table-column>
                <el-table-column prop="CouponActivity" label="活动名称">
                    <template slot-scope="scope">
                        {{scope.row.CouponActivity.Name}}
                    </template>
                </el-table-column>
                
            </el-table>
        </el-card>
    </div>
</template>

<script>
export default {
    name: "",
    data() {
        return {
            tableData: [],
            list:[],
            status: "",
            input: "",
            form:{
                activity_id:'',
                count:""
            },
            rules: {
                activity_id: [
                    { required: true, message: "请选择活动", trigger: "blur" }
                ],
                count: [
                    {
                        required: true,
                        message: "请输入个数",
                        trigger: "blur"
                    }
                ],
            }
        };
    },
    methods: {
        getList() {
            let params = {
                status: this.status,
                pageNo: this.pageNo - 1,
                pageSize: 100
            };
            this.$api("/admin/coupon/activity_list", params).then(res => {
                this.tableData = res.data.list;
                this.totalNo = res.data.page.totalNum;
            });
        },
        onSubmit(){
            let params = {
                notice:'生成成功！'
            };
            Object.assign(params,this.form);
            this.$api("/admin/coupon/code_add", params).then(res => {
                if(res.code == 0){
                    this.list = res.data;
                    this.$refs.form.resetFields();
                }
            });
        }
    },
    created() {
        this.getList();
    }
};
</script>
<style lang='scss' scoped>
@import "../../assets/css/titlecom.scss";
.detaila:hover {
    color: #409eff;
    text-decoration: underline;
}
.search_fun {
    height: 60px;
    position: relative;
    top: 10px;
    float: right;
}
.search_input {
    width: 200px;
}
</style>


